.App {
  text-align: center;
  color: #eee;
}

h1,
h2 {
  color: steelblue;
}

label {
  padding: 4px 10px;
  display: block;
}

button,
input,
select {
  margin-left: 5px;
  padding: 3px 8px;
  font-size: 16px;
  border-radius: 5px;
  border: 1px solid #666;
  background-color: #333;
  color: #eee;
}

textarea,
textarea:focus {
  margin: 0 2%;
  padding: 0;
  margin-top: 10px;
  border: none;
  background: none;
  color: #eee;
  width: 96%;
  height: 120px;
  text-align: center;
  font-family: monospace;
  font-size: 10px;
  white-space: pre;
  overflow-wrap: normal;
  overflow-x: auto;
  cursor: default;
  outline: none;
}

/* GitHub Link */
.githubLink {
  margin-top: 20px;
  text-align: center;
}
.githubLink a {
  text-decoration: none;
  color: #666;
}

/* Scrollbars */
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-track,
::-webkit-scrollbar-corner {
  background: none;
}

::-webkit-scrollbar-thumb {
  border-radius: 4px;
  background: #333;
}

::-webkit-scrollbar-thumb:hover {
  background: #555;
}
